<script>
  import { autoRegister, loadComponentByName } from '$lib/componentLoader.js';

  // Auto-register tất cả file trong folder
  autoRegister('../components/tabs/details/*.svelte');

  let DynamicComponent = null;

  async function open(name) {
    DynamicComponent = await loadComponentByName(name);
  }
</script>

<button on:click={() => open('InvoiceDetail')}>Mở Invoice</button>
<button on:click={() => open('UserDetail')}>Mở User</button>

{#if DynamicComponent}
  <svelte:component this={DynamicComponent} />
{/if}
